
#app{
      width:100%;
      height:100%;
      overflow: hidden;
}
    /* 头部 */
.g-header{
        background: #d5f5f5;
}
.g-header .text{

       text-align: center;
}
   /* 导航 */
.tab{
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      height: 2em;
      background: #d5f5f5;
      
       -webkit-box-align: center;
      
           -ms-flex-align: center;
      
               align-items: center;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
}
.tab .tab-t{
       -webkit-box-flex:1;
           -ms-flex:1;
               flex:1;
       text-align: center;
}
.tab .tab-t{
      color: #000;
}
.router-link-active .tab-t1{
      
        font-size:20px;
        font-weight:bold;
}
.slide-left-enter-active{
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.slide-left-enter{
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}
.slide-right-enter-active{
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.slide-right-enter{
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}   



